.header {
  justify-content: space-between;
  list-style-type: none;
}

.ProfileContainer {
  margin: 10px 10px;
}
.eachUserLink {
  display: flex;
  text-decoration: none;
  list-style: none;
}

.userprofileimage {
  width: 100%;
  max-width: 150px;
  height: auto;
  border-radius: 50%;

  display: block;
}
.avatar {
  margin-right: 10px;
}
.links {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
}
.Userprofile {
  background-color: white;
  border: none;
  padding: 10px 10px;
  box-shadow: 6px 6px 5px grey;
}

@media only screen and (min-width: 600px) {
  .Userprofile {
    width: 50%;
    margin-right: auto;
    margin-left: auto;
  }
}
.information {
  overflow-wrap: break-word;
}
